/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { registerBlockType } = wp.blocks;

/**
 * Internal dependencies
 */
import Edit from './edit';
import { icon_compact } from './icon';

const blockType = 'gravityforms/polls';

registerBlockType( blockType, {
	title:       'Poll',
	description: __( 'Display a poll or poll results on your page.', 'gravityformspolls' ),
	category:    'embed',
	keywords:    [ 'gravity forms', 'form', 'polls' ],
	supports:    {
		customClassName: false,
		className:       false,
		html:            false,
	},
	attributes: {
		formId:                     {
			type: 'string',
		},
		mode:                       {
			type:    'string',
			default: 'poll',
		},
		title:                      {
			type:    'boolean',
			default: true,
		},
		description:                {
			type:    'boolean',
			default: true,
		},
		ajax:                       {
			type:    'boolean',
			default: false,
		},
		tabindex:                   {
			type: 'string',
		},
		style:                      {
			type:    'string',
			default: 'green',
		},
		cookie:                     {
			type:    'string',
			default: null,
		},
		cookieDate:                 {
			type: 'string',
		},
		displayCounts:              {
			type:    'boolean',
			default: true,
		},
		displayResultsLink:         {
			type:    'boolean',
			default: true,
		},
		displayResultsOnSubmission: {
			type:    'boolean',
			default: true
		},
		displayPercentages:         {
			type:    'boolean',
			default: true,
		},
		formPreview:                {
			type:    'boolean',
			default: true,
		},
	},
	icon:        icon_compact,
	edit:        Edit,
	save() {
		return (
			null
		);
	},
} );
